.wordlevel--container {
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
}

.wordlevel--card {
  width: 60vh;
  min-height: 40vw;

  border-radius: 50px;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.wordlevel--card--word--container {
  display: flex;
  flex-wrap: wrap;
}

.wordlevel--weak {
  background-color: palevioletred;
}

.wordlevel--medium {
  background-color: yellow;
}

.wordlevel--strong {
  background-color: greenyellow;
}

.wordlevel--word {
  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 25px;
  border: 1px solid black;

  font-size: 1.5em;

  margin: 0.25em;

  min-width: 100px;
  max-width: 150px;
  min-height: 50px;
  max-height: 100px;

  cursor: pointer;
}

.wordlevel--card--title {
  text-align: center;

  margin: 1em;

  font-size: 1.3em;
  font-weight: 600;
  text-decoration: underline;
}
